import React, { useState } from 'react';
import Home from '../components/Home';
import Header from '../components/Header';

export default () => {
  const [dark, setDark] = useState(true);
  const mainColor = '#30353d';
  return (
    <div className="home">
      <Header
        color={dark ? '#fff' : mainColor}
        dark={dark}
        setLeft={setDark}
      />
      <Home
        color={dark ? '#fff' : mainColor}
      />
      <style jsx>{`
      .home {
        box-sizing: border-box;
        width: 100%;
        height: 100vh;
        background: ${dark ? mainColor : '#fff'};
        padding: 0 25%;
      }
        `}
      </style>
    </div>
  );
};
